import React from 'react';
import './bookme.css';

const BookMe = () => {

  // Funzione per ottenere i prossimi 30 giorni
  const getNext30Days = () => {
    const days = [];
    const today = new Date();
    for (let i = 0; i < 30; i++) {
      const date = new Date(today);
      date.setDate(today.getDate() + i);
      days.push(date);
    }
    return days;
  };

  // Funzione per creare il calendario
  const createCalendar = () => {
    const days = getNext30Days();
    const currentDate = new Date();

    return days.map(day => (
      <div className="day-column" key={day.toDateString()}>
        <div className="day-header">
          {day.toLocaleDateString('it-IT', { weekday: 'long', day: 'numeric', month: 'long' })}
        </div>
        <div className="pills-container">
          {[...Array(24)].map((_, hour) => (
            <div
              className={`hour-pill ${day.toDateString() === currentDate.toDateString() && hour < currentDate.getHours() ? 'hidden' : 'unlocked'}`}
              key={hour}
              onClick={(e) => toggleLock(e)}
            >
              {`${hour}:00`}
            </div>
          ))}
        </div>
      </div>
    ));
  };

  // Funzione per bloccare/sbloccare l'ora
  const toggleLock = (e) => {
    const pill = e.target;
    if (pill.classList.contains('locked')) {
      pill.classList.remove('locked');
      pill.classList.add('unlocked');
    } else {
      pill.classList.remove('unlocked');
      pill.classList.add('locked');
    }
  };

  return (
    <div id="calendar-body">
      <div id="calendar">
        {createCalendar()}
      </div>
    </div>
  );
};

export default BookMe;
